@import "../../global.scss";

#content {
  font-size: 24px;
  margin-right: 50px;
  padding: 7px;
  display: flex;
  border-radius: 10px;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  margin-left: 30px;

  @include mobile {
    display: flex;
    margin-right: 20px;
    margin-left: 20px;
    align-items: center;
    justify-content: center;
    font-size: 18px;
  }

  &.active {
    background-color: $mainColor;
    color: white;
  }
}
